<template>
    <div>
        <h1>Formulario de Contacto</h1>
        <br />
        <div>
            <v-text-field :disabled="enviando" class="fondo" label="Nombre" v-model="nombreContacto"></v-text-field>
            <v-textarea :disabled="enviando" class="fondo" label="Mensaje" v-model="cuerpoMensaje"></v-textarea>
            <v-btn :disabled="enviando" @click="enviarFormulario()">Enviar</v-btn>
        </div>
    </div>
</template>

<script>
import { collection, addDoc, getFirestore } from 'firebase/firestore'
export default {
    name: 'component-name',
    // props: {},
    data: function(){
        return {
            nombreContacto : "",
            cuerpoMensaje: "",
            enviando : false
        }
    },
    // computed: {},
    methods: {
        async enviarFormulario() {
            this.enviando = true
            try {
                let fechaHoy = new Date()
                let fechaHoyString = (fechaHoy.getDate() > 9 ? "" + fechaHoy.getDate() : "0" + fechaHoy.getDate() ) + "/" + (fechaHoy.getMonth() > 8 ? "" + (fechaHoy.getMonth() + 1) : "0" + (fechaHoy.getMonth() + 1)) + "/" + fechaHoy.getFullYear()
                let horaHoyString = (fechaHoy.getHours() > 9 ? "" + fechaHoy.getHours() : "0" + fechaHoy.getHours()) + ":" + (fechaHoy.getMinutes() > 9 ? "" + fechaHoy.getMinutes() : "0" + fechaHoy.getMinutes()) + ":" + (fechaHoy.getSeconds() > 9 ? "" + fechaHoy.getSeconds() : "0" + fechaHoy.getSeconds())
                let mensaje = {Nombre : this.nombreContacto, Mensaje : this.cuerpoMensaje, fecha : fechaHoyString, hora: horaHoyString}
                const db = getFirestore(this.app)
                const docRef = await addDoc(collection(db, "mensajes"), mensaje)
                if (docRef.id != "") {
                    this.nombreContacto = ""
                    this.cuerpoMensaje = ""
                    alert("Mensaje enviado")
                } else {
                    alert("Error al enviar mensaje")
                }
            } catch(error) {
                alert(error)
            }
            this.enviando = false
        }
    }
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

<style scoped>
    .fondo {
        background-color: lightgray;
    }
</style>